.rules {
  color: #CE352B;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 20px;

  h3 {
    text-decoration: underline;
    margin-bottom: 29px;
    font-size: 31px;
  }

  ol {
    max-width: 550px;
    text-align: left;
  }

  li {
    margin-bottom: 5px;

    &::marker {
      width: 30px;
    }
  }
}
