.main {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 10vh;
}

input {
  text-align: center;
  padding: 15px;
  font-size: 20px;
  width: 100%;
  max-width: 380px;
  color: #fff;
  background: rgba(38, 159, 73, 0.5);
  box-shadow: inset 0px 3px 3px rgba(0, 0, 0, 0.25);
  border: 0;
  border-radius: 40px;
  margin-bottom: 10px;
  transition: transform .2s ease-in-out;

  &:hover {
    transform: scale(1.1);
  }

  &::placeholder {
    color: #fff;
  }
}

@media screen and (min-width: 768px) {
  input {
    padding: 30px;
    font-size: 31px;
    border-radius: 65px;
    margin-bottom: 20px;
  }
}